<template>
    <div>
      <!-- 查询 -->
      <common-wrapper>
        <examine-search class="search"/>
      </common-wrapper>
      <nav-button>
        <el-button-group>
          <el-button icon="fdddfont icon-add-new" @click.native="handleAdd()">审批</el-button>
          <el-button icon="fdddfont icon-add-new">查看合同详情</el-button>
        </el-button-group>
      </nav-button>
     <!-- 用户列表 -->
      <common-wrapper>
        <common-table :flex="230">
          <template slot="table">
            <el-table
              ref="roleTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="tableData"
              @selection-change="selected => selectRow = selected">
                <el-table-column type="selection" width="30"></el-table-column>
                <el-table-column type="index"  label="序号" width="50"></el-table-column>
                <el-table-column prop="xyh" label="协议编号" width="120"></el-table-column>
                <el-table-column prop="company" label="乙方" width="100"></el-table-column>
                <el-table-column prop="qx" label="服务期限" width="240"></el-table-column>
                <el-table-column prop="sxrq" label="生效日期" width="100"></el-table-column>
                <el-table-column prop="qsd" label="签署地"  width="100"></el-table-column>
                <el-table-column prop="jf" label="甲方签署人" width="100"></el-table-column>
                <el-table-column prop="yf" label="已方签署人" width="100"></el-table-column>
              <el-table-column prop="status" label="状态"></el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>
      <contract-review
         change.syns="isCreateFormVisible"
         :show.sync="isCreateFormVisible"
      />
    </div>
</template>

<script>
/**
 * Role Management Module
 * ----------------------
 * Author: zhou qi
 * Date: 2018.03.20
 */
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import ExamineSearch from './Components/Search'
import ContractReview from './Components/ContractReview'
import { getDevelopList } from '@/api/develop'

export default {
  name: 'ExamineManger',
  mixins: [mixins],
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: [], // 列表选中列
      editRole: {}, // 需要编辑的角色对象
      isCreateFormVisible: false, // 新增/编辑窗口打开状态
      isShowRoleLineDialog: false,
      isShowRoleMenuDialog: false,
      isListLoading: false, // 列表加载
      isCreate: true, // 编辑窗口展示新增or编辑
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {} // 查询参数
    }
  },
  mounted () {
    getDevelopList().then(res => {
      this.tableData = res.list
      this.pagination.total = res.total
      this.pagination.pageNum = res.pageNum
      this.pagination.pageSize = res.pageSize
      this.isListLoading = false
    })
  },
  methods: {
    // 审批界面
    handleAdd: function () {
      console.log('111')
      this.isCreateFormVisible = true
    }
  },
  // 获取用户列表
  getDevelops (params) {
    this.isListLoading = true
    getDevelopList(params).then(res => {
      this.tableData = res.list
      this.pagination.total = res.total
      this.pagination.pageNum = res.pageNum
      this.pagination.pageSize = res.pageSize
      this.isListLoading = false
    })
  },
  rest (params) {
    const searchParams = this.searchParams = params
    this.getDevelops(
      {
        ...searchParams,
        pageNum: 1,
        pageSize: this.pagination.pageSize
      }
    )
  },
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    ContractReview,
    ExamineSearch
  }
}
</script>



